
.mycontent{
  top: 6.4286rem;
  width: 100%;
  position: absolute;
}
.swiper-container{
  margin-top: .3571rem;
}
.swiper-slide img{
  max-width: 100%;
  height: 8.5714rem;
  margin: 0 auto;
  display: block;
}
.swiper-pagination-bullet{
  width: .5714rem;
  height: .5714rem;
}
.swiper-pagination-bullet-active{
  background: #fff;
}
.list ul{
  padding-left: .7143rem;
  margin: 0;
}
.list li{
  width: 100%;
  height: 4.0714rem;
  border-bottom: 1px solid #e5e5e5;
  position: relative;
  display: table;
}
.left{
  width: 18.2rem;
  padding-right: .3571rem;
  display: table-cell;
  vertical-align: middle;
  padding-left: .3571rem;
  cursor: pointer;
  font-size: 1rem;
  box-sizing: border-box;
}
.right{
  width: 2.5rem;
  height: 100%;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  display: table-cell;
}
.right i{
  display: inline-block;
  width: 1.0357rem;
  height: 1.0357rem;
  background: url("../assets/download_icon.png");
  background-size: 100%;
  margin: 0 auto;
}
.player{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 4.2143rem;
  background: rgba(0,0,0,.9);
  color: #fff;
}
.player p{
  margin: 0;
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
}
.detail{
  width: 8rem;
  margin-top: -3.5rem;
  display: inline-block;
  vertical-align: middle;
}
.detail p:nth-child(1){
  width: 100%;
  line-height: 1.7rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: .8571rem;
  color: #fff;
}
.detail p:nth-child(2){
  line-height: 1.3;
  font-size: .7143rem;
  color: #888;
}
.player .img{
  height: 1.786rem;
  display: inline-block;
}
.player .img img{
  height: 1.786rem;
  width: 1.786rem;
  vertical-align: middle;
  margin-top: -3rem;
  margin-right: 0.5rem;
}



